*,
*::after,
*::before {
  box-sizing: border-box;
}

html {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
  font-size: 18px;
  text-rendering: optimizeLegibility;
  // background: linear-gradient(45deg, #1D1924, #6c5b7b);
  background: linear-gradient(25deg, rgba(29, 25, 36, 1), rgba(29, 25, 36, 0.8));
}

body { 
  height: 100vh; 
  display: flex; 
  justify-content: space-around; 
  align-items: center; 
}

h4, h5, h6 {
  margin-block-start: 0.2em;
  margin-block-end: 0.2em;
  margin-inline-start: 0;
  margin-inline-end: 0;
}

ul {
  padding-inline-start: 0;
}

figure {
  margin: 0;
}

a {
  color: white;
  text-decoration: none;
}

button {
  background-color: transparent;
  border: none;
  cursor: pointer;
  outline: none;
  padding: 5px;
}

input {
  background: none;
  border: none;
  outline: none;
  caret-color: white;
}

